<script>
import $ from "jquery";
import lessons from "./lesson";
export default {
  components: {
    CreateStudent: () => import("@/tkViews/components/Modal/CreateStudent"),
  },
  data() {
    return {
      showCreate: false,
      fullName: "John Michael",
      email: "john.michael@gmail.com",
      phone: "+1 405 876 4436",
      isInvalid: false,
      lessons,

      showEditContact: false,
      editLesson: false,
      showEditLessonDetail: false,
      showAddNewLesson: false
    };
  },
  methods: {
    validationEmail() {
      let self = this;
      if (self.$tools.isNull(self.email)) {
        self.isInvalid = false;
      } else {
        self.isInvalid = !self.$tools.isEmail(self.email);
      }
    },
    toggleEditLesson() {
      let self = this;
      self.editLesson = !self.editLesson;
      $("#addNewLesson").slideToggle();
    },
    deleteLesson(index) {
      this.lessons.splice(index, 1);
    },
    showLessonDetail() {
      let self = this
      if(!self.editLesson){
        self.showEditLessonDetail = true
      }
    }
  },
};
</script>